/**
 * 
 */
import EquipType from './EquipType';

const EquipPosition = Object.freeze({
  HELM: EquipType.HELM,                   // 
  CHEST: EquipType.CHEST,                 // 
  WAIST: EquipType.WAIST,                 // 
  BANGLE: EquipType.BANGLE,               // 
  PANTS: EquipType.PANTS,                 // 
  BOOTS: EquipType.BOOTS,                 // 
  AMULET: EquipType.AMULET,               // 
  PENDANT: EquipType.PENDANT,             // 
  RING_1: `${EquipType.RING}_1`,          // 1
  RING_2: `${EquipType.RING}_2`,          // 2
  MELEE_WEAPON: EquipType.MELEE_WEAPON,   // 
  RANGE_WEAPON: EquipType.RANGE_WEAPON,   // 

  all() {
    // 
    let positions = [
      {type: this.MELEE_WEAPON, label: "", AucGenre: 1},
      {type: this.RANGE_WEAPON, label: "", AucGenre: 2},
      {type: this.HELM, label: "", AucGenre: 3, AucSubType: 2},
      {type: this.CHEST, label: "", AucGenre: 3, AucSubType: 1},
      {type: this.WAIST, label: "", AucGenre: 3, AucSubType: 3},
      {type: this.BANGLE, label: "", AucGenre: 3, AucSubType: 6},
      {type: this.PANTS, label: "", AucGenre: 3, AucSubType: 4},
      {type: this.BOOTS, label: "", AucGenre: 3, AucSubType: 5},
      {type: this.AMULET, label: "", AucGenre: 4, AucSubType: 1},
      {type: this.PENDANT, label: "", AucGenre: 4, AucSubType: 3},
      {type: this.RING_1, label: "", AucGenre: 4, AucSubType: 2},
      {type: this.RING_2, label: "", AucGenre: 4, AucSubType: 2},
    ];

    // 
    let output = {};
    for (let i in positions) {
      let position = positions[i];
      output[position.type] = position;
    }

    return output;
  }
})

export default EquipPosition;