const EquipType = Object.freeze({
  HELM: 'helm',                   // 
  CHEST: 'chest',                 // 
  WAIST: 'waist',                 // 
  BANGLE: 'bangle',               // 
  PANTS: 'pants',                 // 
  BOOTS: 'boots',                 // 
  AMULET: 'amulet',               // 
  PENDANT: 'pendant',             // 
  RING: 'ring',                   // 
  MELEE_WEAPON: 'melee_weapon',   // 
  RANGE_WEAPON: 'range_weapon',   // 
  AMMO_POUCH: 'ammo_pouch',       // 

  all() {
    return [
      this.HELM,
      this.CHEST,
      this.WAIST,
      this.BANGLE,
      this.PANTS,
      this.BOOTS,
      this.AMULET,
      this.PENDANT,
      this.RING,
      this.MELEE_WEAPON,
      this.RANGE_WEAPON,
      this.AMMO_POUCH,
    ]
  },
})

export default EquipType;